	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	export default {
		data() {
			const currentDate = this.getDate({
			format: true
			})
			return {
				array: ['中国', '美国', '巴西', '日本'],
                index: "",
				selectValue:"",   
				imageList:[],
				date: currentDate,
				Id:"",
				list:[],
				pic:[],
				
			}
		},
		onLoad:function(option){
			var _self = this
			_self.Id = option.id
			_self.getInfo()
		},
		methods: {
			bindDateChange: function(e) {
			console.log(e)
			this.date = e.target.value
			},
			bindPickerChange: function(e) {
            console.log('picker发送选择改变，携带值为', e.target.value)
            this.index = e.target.value
			},
			previewImage: function(e) {
				uni.previewImage({
					current: e,
					urls: this.pic
				})
			},
			chooseImage: async function() {
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {
							return;
						}
					}
					uni.chooseImage({
						success: (res) => {
							this.imageList = this.imageList.concat(res.tempFilePaths);
						}
					})
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = [];
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
				getDate:function(type) {
				 const date = new Date();
				 let year = date.getFullYear();
				 let month = date.getMonth() + 1;
				 let day = date.getDate();
				
				 if (type === 'start') {
				     year = year - 60;
				 } else if (type === 'end') {
				     year = year + 2;
				 }
				 month = month > 9 ? month : '0' + month;;
				 day = day > 9 ? day : '0' + day;
				 return `${year}-${month}-${day}`;
				},
				getInfo:function(){
					var _self =this
					 uni.request({
					 	url:_self.$api+"dockingManager/cardQuery",
						method:"GET",
						data:{
							optionId:uni.getStorageSync("openId"),
							id:_self.Id,
							branch:1,
							live:1
						},
						success:function(res){
							console.log(res)
							_self.list = res.data[0]
							if(res.data[0].shrink==""){
								_self.pic= []
							}else{
								_self.pic = res.data[0].shrink.split(',')
							}
						}
					 })
				},
				audit:function(type){
					var _self =this
					uni.request({
						url:_self.$api+"dockingManager/cardTrial",
											method:"GET",
											data:{
												id:_self.Id,
												optionId:uni.getStorageSync("openId"),
												state:type
											},
											success:function(res){
												console.log(res)
												if(res.data == 1){
													uni.showToast({
														title:"操作成功",
														success:function(){
															setTimeout(function(){
																uni.navigateTo({
																	url:"../auditManager/auditManager"
																})
															},1000)
														}
													})
												}else{
													uni.showToast({
														title:"操作失败"
														})
														return false
												}
											}
					})
				}
		},
		 components: {uniCollapse,uniCollapseItem},
		  computed: {
		     startDate() {
		         return this.getDate('start');
		     },
		     endDate() {
		         return this.getDate('end');
		     }
		 },
	}
